import Title from "./Title"
import Search from "./Search"
import Notice from "./Notice"

export default function Header() {
    const title = "Home"
    return (
        <>
            <div className="w-full sticky top-0 flex justify-center bg-gray-300">
                <Title title={title} />
                <Notice />
                <Search />
            </div>
        </>
    )
}